* {
  padding: 0;
  margin: 0;
  list-style-type: none;
}

html {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  font-size: 62.5%;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  width: 100%;
  height: 100%;
  font-family: sans-serif;
}

a {
  text-decoration: none;
}
