div#root {
  display: flex;
  align-items: center;

  div#navContainer {
    height: 100vh;
    width: 25rem;
  }

  div#mainContainer {
    height: 100vh;
    flex-grow: 1;
    overflow-y: scroll;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;

    main {
      height: 100%;
      width: 100%;
      padding: 4rem 4rem;
    }
  }
}

@media (max-width: 960px) {
  div#root {
    display: flex;
    align-items: center;
    flex-direction: column-reverse;
    height: 100vh;

    div#navContainer {
      width: 100vw;
      height: 7rem;
      z-index: 99;
    }

    div#mainContainer {
      flex-grow: 1;
      width: 100vw;
      overflow-y: scroll;

      main {
        height: 100%;
        width: 100%;
        padding: 0;
      }
    }
  }
}
