div.web-block {
  position: relative;
  border-radius: 0.4rem;
  box-shadow: 0px 0px 7px rgba(0, 0, 0, 0.5);
  height: 21rem;

  div.web-detail-container {
    cursor: default;
    position: fixed;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    background-color: rgba($color: #000000, $alpha: 0.5);
    z-index: 99;
    display: flex;
    justify-content: center;
    align-items: center;

    div.web-detail {
      flex-grow: 1;
      margin: 1rem;
      max-width: 60rem;
      background-color: white;
      border-radius: 1rem;

      div.detail-title {
        background-color: #0080ff;
        color: white;
        border-radius: 1rem 1rem 0 0;
        padding: 1rem 1.5rem;
        display: flex;
        align-items: center;
        justify-content: space-between;

        i {
          cursor: pointer;
        }
      }

      div.images-container {
        position: relative;
        height: 40rem;
        overflow: hidden;

        div.digital-display {
          position: absolute;
          bottom: 1rem;
          left: 28rem;
          padding: 0.5rem 1rem;
          color: white;
          background-color: rgba($color: #000000, $alpha: 0.8);
          border-radius: 1.3rem;
          z-index: 999;
        }

        i {
          position: absolute;
          height: 100%;
          display: flex;
          align-items: center;
          color: rgba($color: #000000, $alpha: 0.6);
          background-color: rgba($color: #000000, $alpha: 0.2);
          cursor: pointer;
          z-index: 999;
        }

        i.fa-arrow-left {
          left: 0;
        }

        i.fa-arrow-right {
          right: 0;
        }

        ul {
          display: flex;
          align-items: center;

          li.image-container {
            height: 40rem;
            width: 60rem;
            display: flex;
            align-items: center;
            justify-content: center;

            img {
              height: 40rem;
              width: 60rem;
            }
          }
        }

        ul.showing-index-1 {
          transform: translateX(-60rem);
        }

        ul.showing-index-2 {
          transform: translateX(-120rem);
        }

        ul.showing-index-3 {
          transform: translateX(-180rem);
        }

        ul.showing-index-4 {
          transform: translateX(-240rem);
        }
      }

      div.detailed-introduction {
        padding: 2rem;

        p {
          text-indent: 2em;
          font-size: 2rem;
        }
      }
    }
  }

  div.web-resume {
    position: absolute;
    margin: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    transition: opacity 150ms ease-in-out;
    background-color: rgba($color: #000000, $alpha: 0.9);
    border-radius: 0.4rem;

    h1 {
      color: white;
      padding: 1rem 1.5rem;
    }

    p {
      color: white;
      padding: 0 1.5rem;
      font-size: 1.8rem;
    }
  }

  &:hover {
    cursor: pointer;

    div.web-resume {
      opacity: 1;
    }
  }

  div.block-title {
    background-color: #0080ff;
    color: white;
    border-radius: 4px 4px 0px 0px;
    padding: 1rem 1.5rem;
  }

  div.block-content {
    display: flex;
    align-items: center;
    justify-content: center;

    img {
      height: 95%;
      width: 95%;
    }
  }
}

@media (max-width: 960px) {
  div.web-block {
    div.web-detail-container {
      div.web-detail {
        max-width: 30rem;

        div.images-container {
          height: 20rem;

          div.digital-display {
            left: 13rem;
          }

          ul {
            li.image-container {
              width: 30rem;
              height: 20rem;
              img {
                width: 30rem;
                height: 20rem;
              }
            }
          }

          ul.showing-index-1 {
            transform: translateX(-30rem);
          }

          ul.showing-index-2 {
            transform: translateX(-60rem);
          }

          ul.showing-index-3 {
            transform: translateX(-90rem);
          }

          ul.showing-index-4 {
            transform: translateX(-120rem);
          }
        }
      }
    }
  }
}
