div.message-block {
  margin-top: 35px;
  border-radius: 4px;
  box-shadow: 0px 0px 7px rgba(0, 0, 0, 0.5);

  div.block-title {
    background-color: #0080ff;
    color: white;
    border-radius: 4px 4px 0px 0px;
    padding: 10px 15px;
  }

  div.block-content {
    padding: 0px 25px;
    font-size: 18px;
    padding-bottom: 10px;

    div {
      margin-top: 10px;
    }

    .content-title {
      font-weight: bold;
    }
  }
}
