#cp-nav {
  height: 100vh;
  background-color: #0080ff;
  box-shadow: 5px 0 30px rgba(00, 80, 255, 0.7);
  display: flex;
  justify-content: space-evenly;
  flex-direction: column;
  align-items: center;
  animation: navMove_pc 500ms cubic-bezier(0.42, 0.98, 0.76, 1.01) forwards;
}

/*nav-block1*/
#nav-block1 {
  text-align: center;

  #certificatePhoto {
    border-radius: 50%;
    border: solid 5px #46a3ff;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.7);
  }

  #name {
    margin-top: 10px;
    color: white;
    font-size: 40px;
  }
}

/*nav-block2*/
#nav-block2 {
  height: 80px;

  .li-route {
    color: white;
    font-size: 25px;
    margin-top: 5px;
  }
}

/*nav-block3*/
#icon-contact {
  i {
    color: white;
  }
}

@media (max-width: 960px) {
  #mobile-nav {
    width: 100vw;
    height: 100%;
    background-color: #0080ff;
    box-shadow: 5px 0 30px rgba(00, 80, 255, 0.7);
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    animation: navMove_mb 500ms cubic-bezier(0.42, 0.98, 0.76, 1.01) forwards;

    img {
      border-radius: 50%;
      border: solid 5px #46a3ff;
      box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.7);
      margin-bottom: 7vh;
    }

    i {
      color: white;
    }
  }
}

@keyframes navMove_pc {
  from {
    transform: translateX(-25rem);
  }
  to {
    transform: translateX(0);
  }
}

@keyframes navMove_mb {
  from {
    transform: translateY(25rem);
  }
  to {
    transform: translateY(0);
  }
}
