div#websiteWorks {
  padding: 3rem 12rem;

  div#wwTitle {
    p {
      font-size: 30px;
      border-bottom: solid 1px black;
    }
  }

  div#webBlocks {
    margin-top: 3rem;
    display: grid;
    grid-template-columns: repeat(auto-fill, 25rem);
    grid-template-rows: repeat(auto-fill, 21rem);
    grid-gap: 2rem 3rem;
    justify-content: center;
  }
}

@media (max-width: 960px) {
  div#websiteWorks {
    padding: 3rem 1rem;

    div#wwTitle {
      p {
        font-size: 30px;
        border-bottom: solid 1px black;
      }
    }

    div#webBlocks {
      width: 100%;
      margin-top: 3rem;
      margin-bottom: 3rem;
      display: grid;
      grid-template-columns: repeat(auto-fill, 25rem);
      grid-template-rows: repeat(auto-fill, 21rem);
      grid-gap: 2rem 3rem;
      justify-content: center;
    }
  }
}
